@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;700&family=Roboto:wght@300;400;500;900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-second);
  background: var(--white);
  color: var(--black);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.nav-item.active a {
  color: var(--red);
}

:root {
  --red:#E52A34;
  --white:#FFFFFF;
  --dark-white:#ECEBEB;
  --black:#1C1919;
}

:root {
  --font-main: "Dancing Script", cursive;
  --font-second: "Roboto", sans-serif;
}

.container {
  max-width: 75rem;
  width: 90%;
  margin: 0 auto;
}

ul {
  margin: 0;
}

.nav-item {
  font-size: 0.875rem;
  font-weight: 500;
}

.logo {
  font-size: 1.5rem;
  font-family: var(--font-main);
  color: white;
}

nav {
  width: 100%;
  min-height: 8vh;
  padding: 1.25rem 1.875rem;
  background-color: var(--black);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav .logo span {
  color: var(--red);
}
nav .nav-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
nav .nav-links.container {
  max-width: 37.5rem;
  width: 100%;
  margin: 0 auto;
}
nav .nav-item a {
  color: var(--white);
  transition: color 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
nav .nav-item a:hover {
  color: var(--red);
}
nav .burger {
  display: none;
  cursor: pointer;
}
nav .burger div {
  transition: all 1.2s cubic-bezier(0.075, 0.82, 0.165, 1);
  width: 35px;
  height: 3px;
  background: var(--white);
  border-radius: 100px;
}
nav .burger div:nth-child(1) {
  transform: translateY(-10px);
}
nav .burger div:nth-child(3) {
  transform: translateY(10px);
}
nav .burger:hover div:nth-child(1) {
  transform: translateY(-6px);
}
nav .burger:hover div:nth-child(3) {
  transform: translateY(6px);
}
@media (max-width: 71.8125em) {
  nav nav {
    justify-content: space-between;
    background-color: rebeccapurple;
  }
  nav .burger {
    display: block;
  }
  nav .nav-links {
    height: 0;
    overflow: hidden;
    flex-direction: column;
    position: absolute;
    align-items: flex-start;
    top: 10vh;
    left: 0;
    background-color: var(--black);
  }
  nav .nav-links.container {
    max-width: 100%;
    width: 100%;
  }
  nav .nav-links .nav-item {
    padding: 1.875rem;
  }
}